import React from "react";
import 'antd/dist/antd.css'
import {NavMenu,MenuItem} from "../../../common/NavMenu/NavMenu";
import {withRouter} from "react-router";

interface State{
    menuItem:MenuItem[],
    curren:string
}
class MenuBar extends React.Component<any, any>{
    state:State={
        menuItem:[
            {key:"1",title:"",click:()=>{this.props.history.push("/")}},//click
            {key:"2",title:"",click:()=>{this.props.history.push("/user/personal")}},
            {key:"3",title:"",click:()=>{this.props.history.push("/user/songs")}},
            {key:"4",title:"",click:()=>{this.props.history.push("/user/rank")}},
            {key:"5",title:"",click:()=>{this.props.history.push("/user/singer")}},
            {key:"6",title:"",click:()=>{this.props.history.push("/user/new")}},
        ],
        curren:"1"
    }
    render() {
        return (
            <div>
                <NavMenu menu={this.state.menuItem} />
            </div>
        )
    }
}

export default withRouter(MenuBar)