import React from "react";
import 'antd/dist/antd.css'
import EventEmitter from "events";
import VolumeBar from "./VolumeBar/VolumeBar";
import {Image,Tooltip} from "antd";
import {HeartOutlined,HeartTwoTone,MenuUnfoldOutlined,StepBackwardOutlined,PlayCircleOutlined,StepForwardOutlined,
        MenuOutlined,PauseOutlined,SoundOutlined,AudioMutedOutlined,UpOutlined,DownOutlined} from '@ant-design/icons'
import {withRouter} from "react-router";
import {getSong} from "../../../../apis/songListDetailPage";
import {isLogin} from "../../MainPage/MainPage";

const imgDefault="data:image/png;base64,"
const Event=new EventEmitter()
const style=require("./PlayBar.css")

interface State{
    cur:SongInfo
    like:boolean
    player:any,
    isPlaying:boolean,
    isPause:boolean,
    progressBG:any,
    progressBar:any,
    curTime:string,
    endTime:string,
    mouseDown:boolean,
    isMute:boolean,
    isShowVolume:boolean,
    input:any,
    musicUrl:string,
    musicID:number,
    isShowingDetail:boolean,
    keyword:string,
    selectedRow:number,
    songListID:number,
    albumID:number
}
interface SongInfo{
    img:string,
    title:string,
    singer:string,
}
class PlayBar extends React.Component<any, any>{
    state:State={
        cur:{
            img:"../../../../assets/icon.png",
            title:"title",
            singer:"singer"
        },
        like:false,
        player:React.createRef(),
        isPlaying:false,
        isPause:false,
        progressBG:React.createRef(),
        progressBar:React.createRef(),
        curTime:"00:00",
        endTime:"00:00",
        mouseDown:false,
        isMute:false,
        isShowVolume:false,
        input:React.createRef(),
        musicUrl:"",
        //musicUrl:'http://m701.music.126.net/20211209230650/154ee87f8eee38133f3a81c79c6564ae/jdymusic/obj/wo3DlMOGwrbDjj7DisKw/9865019573/86ce/dc40/9037/afbde497140b6aa141281dc2686bc750.mp3',
        musicID:Infinity,
        isShowingDetail:false,
        keyword:"",
        selectedRow:Infinity,
        songListID:Infinity,
        albumID:Infinity
    }

    componentDidMount() {
        this.state.player.current.focus()
        this.state.progressBar.current.focus()
        this.state.progressBG.current.focus()
        const audio=document.getElementById("audio")
        if(audio)
        {audio.addEventListener("timeupdate",this.updateProgress)}
        Event.addListener("mute",()=>{this.setState({isMute:true})})
        this.props.playMusicEvent.addListener("play", (url:string,id:number,keyword:string,selectedRow:number)=>
            {
                this.setState(()=>({musicUrl:url,musicID:id,keyword:keyword,selectedRow:selectedRow,isPause:false,endTime:"00:00",isPlaying:true}),()=>{
                    this.getSongInfo()
                    this.play()
                })
            })
        this.props.playSongListEvent.addListener("play",(id:number, url:string, songListID:number)=>{
            this.setState(()=>({musicUrl:url, musicID:id, keyword:"$", selectedRow:Infinity, isPause:false, songListID:songListID,endTime:"00:00",isPlaying:true}),
                ()=>{
                this.getSongInfo()
                this.play()
            })
        })
        this.props.playSingleSongEvent.addListener("play",(id:number,url:string, path:string)=>{
            this.setState(()=>({musicUrl:url, musicID:id, keyword:path, selectedRow:Infinity, isPause:false,endTime:"00:00",isPlaying:true}),()=>{
                this.getSongInfo()
                this.play()
            })
        })
        this.props.playAlbumSongEvent.addListener("play",(id:number, url:string, albumID:number)=>{
            this.setState(()=>({musicUrl:url, musicID:id, keyword:"$$", selectedRow:Infinity, isPause:false, albumID:albumID,endTime:"00:00",isPlaying:true}),()=>{
                this.getSongInfo()
                this.play()
            })
        })
        this.props.replayEvent.addListener("play",(id:number, url:string)=>{
            this.setState(()=>({musicUrl:url, musicID:id, keyword:"$replay", selectedRow:Infinity, isPause:false,endTime:"00:00",isPlaying:true}),()=>{
                this.getSongInfo()
                this.play()
            })
        })
    }

    getSongInfo=()=>{
        getSong(this.state.musicID).then(res=>{
            const data=res.data.songs[0]
            this.setState({cur:{img:data.al.picUrl,title:data.name,singer:data.ar[0].name}})
        })
    }

    play=()=>{
        //window.localStorage.setItem("song",URL.createObjectURL(this.state.input.current.files[0]))
        const player:any=this.state.player
        if(!this.state.isPause)
        {player.current.src=this.state.musicUrl}
        this.setState(()=>({isPlaying:true,isPause:false}),()=>{player.current.play()})
    }

    pause=()=>{
        const player:any=this.state.player
        player.current.pause()
        this.setState({isPlaying:false,isPause:true})
    }

    setTime=(time:number,setCur:boolean)=>{
        let min=Math.floor(time/60)
        let minStr=(min<10)?"0"+min:min
        let sec=Math.floor(time-min*60)
        let secStr=(sec<10)?"0"+sec:sec
        if(setCur){this.setState({curTime:minStr+":"+secStr})}
        else {this.setState({endTime:minStr+":"+secStr})}
    }

    updateProgress=()=>{
        const player=this.state.player
        const bar=this.state.progressBar
        let cur=player.current.currentTime
        bar.current.style.width=(100*cur/player.current.duration)+"%"
        this.setTime(cur,true)
        if(this.state.endTime==="00:00" && !Number.isNaN(player.current.duration))
        {this.setTime(player.current.duration,false)}
        if(this.state.curTime===this.state.endTime && this.state.curTime!=="00:00")
        {this.setState(()=>({isPlaying:false}),()=>{})}

        this.props.playWithLyricsEvent.emit("updateLyric",cur)

        //console.log(this.state.isPlaying,this.state.isPause);
    }

    clickProgress=(e:any)=>{
        let start=this.state.progressBG.current.offsetLeft
        let end=this.state.progressBG.current.offsetWidth+start
        let cur=e.clientX
        if(cur>=start && cur<=end)
        {
            const bar=this.state.progressBar
            const player=this.state.player
            let ratio=((cur-start)/(end-start))
            bar.current.width=(100*ratio)+"%"
            let curTime=player.current.duration*ratio
            player.current.currentTime=curTime
            this.setTime(curTime,true)
            if(cur===end)
            {this.setState({isPlaying:false})}
        }
    }

    setMute=(mute:boolean)=>{
        const player=this.state.player
        if(mute)
        {
            player.current.volume=0
            this.setState({isMute:true})
            Event.emit("setVolumeZero")
        }
        else
        {
            player.current.volume=0.5
            this.setState({isMute:false})
            Event.emit("setVolumeHalf")
        }
    }

    showDetail=()=>{
        if(!this.state.isShowingDetail)
        {
            this.props.history.push("/detail/"+this.state.musicID+"/"+this.state.keyword)
            this.setState({isShowingDetail:true})
        }
        else
        {
            let keyword=this.state.keyword
            this.setState({isShowingDetail:false})
            if(keyword==="$") this.props.history.push("/song-list/"+this.state.songListID)
            else if(keyword==="$$")
            {
                //console.log("$$",this.state.albumID);
                this.props.history.push("/album/"+this.state.albumID)
            }
            else if(keyword==="$rank")this.props.history.push("/user/rank")
            else if(keyword==="$new-song")this.props.history.push("/user/new-song")
            else if(keyword==="$user" || keyword==="$another-song")this.props.history.push("/user")
            else if(keyword==="$replay")this.props.history.push("/play-history")
            else {this.props.history.push("/search/"+this.state.keyword+"/"+this.state.selectedRow)}
        }
    }

    getUserSongs=()=>{
        if(isLogin && window.localStorage.getItem("id")!==null)
        {this.props.history.push("/play-history")}
        else {this.props.history.push("/login")}
    }

    render() {
        return (
            <div id="playRoot">
                <style ref={style} scoped/>
                <div id='songInfo'>
                    {
                        this.state.musicID===Infinity?
                            <Image
                                src={this.state.cur.img}
                                width="50px" height="50px"
                                fallback={imgDefault}
                                onClick={this.showDetail}
                            />:
                            <div style={{position:"relative"}}>
                                <Tooltip title={this.state.isShowingDetail?"":""}>
                                    <img
                                        src={this.state.cur.img}
                                        style={{width:"50px",height:"50px",objectFit:"cover",borderRadius:"3px",cursor:'pointer'}}
                                        onClick={this.showDetail}
                                    />
                                </Tooltip>
                                {
                                    this.state.isShowingDetail?
                                        <div style={{position:"absolute",top:"6px",left:"18px",color:'white'}}>
                                            <div>
                                                <DownOutlined/>
                                            </div>
                                            <div>
                                                <DownOutlined/>
                                            </div>
                                        </div>:
                                        <div style={{position:"absolute",top:"6px",left:"18px",color:'white'}}>
                                            <div>
                                                <UpOutlined/>
                                            </div>
                                            <div>
                                                <UpOutlined/>
                                            </div>
                                        </div>
                                }
                            </div>
                    }
                    <div id="titleAndLikeAndSinger">
                        <div id="titleAndLike">
                            <Tooltip title={this.state.cur.title}>
                                <div style={{whiteSpace:"nowrap",wordBreak:"keep-all",overflow:"hidden",textOverflow:"ellipsis",cursor:"pointer"}}>
                                    {this.state.cur.title}
                                </div>
                            </Tooltip>
                            {
                                this.state.like?
                                    <HeartTwoTone twoToneColor="red" onClick={()=>{this.setState({like:!this.state.like})}}/>:
                                    <HeartOutlined onClick={()=>{this.setState({like:!this.state.like})}}/>
                            }
                        </div>
                        <Tooltip title={this.state.cur.singer}>
                            <div style={{whiteSpace:"nowrap",wordBreak:"keep-all",overflow:"hidden",textOverflow:"ellipsis",cursor:"pointer"}}>
                                {this.state.cur.singer}
                            </div>
                        </Tooltip>
                    </div>
                </div>
                <div id="play">
                    <div id="playButtons">
                        <MenuUnfoldOutlined className="playItem"/>
                        <StepBackwardOutlined  className="playItem"/>
                        {
                            this.state.isPlaying?
                                <Tooltip title="">
                                    <PauseOutlined onClick={this.pause}/>
                                </Tooltip> :
                                <Tooltip title="">
                                    <PlayCircleOutlined  onClick={()=>this.play()} className="playItem"/>
                                </Tooltip>
                        }
                        <StepForwardOutlined  className="playItem"/>
                        <Tooltip title="">
                            <MenuOutlined  className="playItem"/>
                        </Tooltip>
                    </div>
                    <div id="playProgress">
                        <audio ref={this.state.player} id="audio"/>
                        <div id="playSensing"
                             onClick={(e)=>{this.clickProgress(e)}}
                             onMouseDown={()=>{this.setState({mouseDown:true})}}
                             onMouseMove={(e)=>{if(this.state.mouseDown){this.clickProgress(e)}}}
                             onMouseUp={()=>{this.setState({mouseDown:false})}}
                        >
                            {this.state.curTime}
                            <div id="playBG" ref={this.state.progressBG}>
                                <div id="playBar" ref={this.state.progressBar}/>
                                <div id="playThumb"/>
                            </div>
                            {this.state.endTime}
                        </div>
                    </div>
                </div>
                <div
                    id="volume"
                    onClick={()=> {this.setState({isShowVolume:!this.state.isShowVolume})}}
                >
                    {
                        this.state.isMute?
                            <Tooltip title="">
                                <AudioMutedOutlined
                                    onClick={(e)=>{this.setMute(false);e.stopPropagation()}}
                                />
                            </Tooltip>
                            :
                            <Tooltip title="">
                                <SoundOutlined
                                    onClick={(e)=>{this.setMute(true);e.stopPropagation()}}
                                />
                            </Tooltip>
                    }
                </div>
                {
                    this.state.isShowVolume?
                    <VolumeBar player={this.state.player} event={Event} show={true}/>:
                    <VolumeBar player={this.state.player} event={Event} show={false}/>
                }
                <div id="playSongsList" onClick={this.getUserSongs}>
                    <Tooltip title="">
                        <MenuUnfoldOutlined />
                    </Tooltip>
                </div>
            </div>
        )
    }
}

export default withRouter(PlayBar)
