import React from "react";
import 'antd/dist/antd.css'
import {NavMenu,MenuItem} from "../../../common/NavMenu/NavMenu";
import {withRouter} from "react-router";

interface State{
    menuItem:MenuItem[]
}
class MenuBar extends React.Component<any, any>{
    state:State={
        menuItem:[
            {key:"1",title:"",click:()=>{this.props.history.push("/search/singles/"+this.props.keyword)}},//click
            {key:"2",title:"",click:()=>{this.props.history.push("/search/singers/"+this.props.keyword)}},
            {key:"3",title:"",click:()=>{this.props.history.push("/search/albums/"+this.props.keyword)}},
            {key:"4",title:"",click:()=>{this.props.history.push("/search/videos/"+this.props.keyword)}},
            {key:"5",title:"",click:()=>{this.props.history.push("/search/song-lists/"+this.props.keyword)}},
            {key:"6",title:"",click:()=>{this.props.history.push("/search/lyrics/"+this.props.keyword)}},
            {key:"7",title:"",click:()=>{this.props.history.push("/search/users/"+this.props.keyword)}},
        ]
    }
    render() {
        return (
            <div>
                <NavMenu menu={this.state.menuItem}/>
            </div>
        )
    }
}

export default withRouter(MenuBar)